@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@500&display=swap);
body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

.wrapper{display:grid;grid-template-columns:repeat(auto-fit, minmax(317px, 317px));grid-gap:25px;background-color:#e5e5e5;justify-content:center}

.card-wrapper{width:312px;height:424px;border-radius:3px;box-shadow:0px 0px 0px rgba(34,44,53,0.05),0px 1px 3px rgba(34,44,53,0.15);justify-self:center;background-color:#fff}.image-block{display:flex;flex-direction:row;overflow-x:scroll;margin-left:6px;padding-top:8px;-ms-scroll-chaining:none;overscroll-behavior:contain;scrollbar-width:none;-ms-overflow-style:none;-webkit-overflow-scrolling:touch}.image-block::-webkit-scrollbar{display:none}.post{margin-left:2px;margin-right:2px}.info-block{height:156px}.avatar{margin:auto;border-radius:50%;margin-top:-44px}.info-block a{text-decoration:none}.info-wrapper{position:relative;display:flex;flex-direction:column;justify-content:space-between;align-items:center;height:139px}.name,.age-gender-type,.content-topic,.followers,.name-social{margin:0}.name{font-family:'Roboto', sans-serif;font-style:normal;font-weight:500;font-size:16px;line-height:24px;text-align:center;color:#222C35;position:absolute;top:51px}.age-gender-type{font-family:'Roboto', sans-serif;font-style:normal;font-weight:normal;font-size:14px;line-height:20px;text-align:center;color:#919CAB;position:absolute;top:73px}.content-topic{font-family:'Roboto', sans-serif;font-style:normal;font-weight:normal;font-size:16px;line-height:24px;text-align:center;color:#636F81;margin-bottom:10px}.divider{width:48px;height:2px;background:#E1E6EA}.social-block{display:flex;flex-direction:row;justify-content:space-around;width:266px;height:69px;margin:0 auto}.item-social{display:flex;flex-direction:column;justify-content:space-between;align-items:center}.followers{font-family:'Roboto', sans-serif;font-style:normal;font-weight:500;font-size:14px;line-height:20px;text-align:center;color:#222C35;margin-top:7px}.name-social{font-family:'Roboto', sans-serif;font-style:normal;font-weight:normal;font-size:14px;line-height:20px;text-align:center;color:#919CAB}

